<template>
  <div class="focus">
    <PersonalHeader></PersonalHeader>
    <div class="wrapper clearfix content">
      <PersonalMenu class="fl" currentMenu="2"></PersonalMenu>
      <div class="fr right">
        <div class="header clearfix">
          <div class="tab fl currentTab">我的关注</div>
        </div>
        <div class="per">
          <router-link class="item" v-for="item in data" :key="'focus'+item.id" :to="'detail?id='+item.id">
              <img :src="item.img" alt="">
              <div class="right">
                <div class="name clearfix">
                  <span class="fl">{{item.item_name}}</span>
                  <span class="money fr">￥{{item.money}}万</span>
                </div>
                <div class="detail">
                  <div class="info">
                    所属行业：<span>{{item.cate_name}}</span>
                  </div>
                  <div class="info">
                    直营店：<span>{{item.shop_count}}家</span>
                  </div>
                  <div class="info">
                    申请人数：<span>{{item.apply}}</span>
                  </div>

                  <div class="info">
                    加盟店：<span>{{item.fran_store_num}}家</span>
                  </div>
                </div>
              </div>
            <div class="btns" >
              <el-button type="primary" @click.prevent="dialogFormVisible = true,productid = item.id">立即咨询</el-button>
              <el-button :data-id="item.id" @click.prevent="cancelFocus">取消关注</el-button>
            </div>
          </router-link >
        </div>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="count"
          class="pages"
          @current-change="pageChange"
        >
        </el-pagination>
      </div>
    </div>

    <!-- 咨询弹窗  -->
    <el-dialog title="立即咨询" :visible.sync="dialogFormVisible" center width="30%">
      <el-form :model="form">
        <el-form-item label="姓名" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off" placeholder="请输入您的姓名"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth">
          <el-radio v-model="radio" label="1">男</el-radio>
          <el-radio v-model="radio" label="2">女</el-radio>
        </el-form-item>
        <el-form-item label="手机" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off" placeholder="请输入您的手机号"></el-input>
        </el-form-item>
        <el-form-item label="图片验证" :label-width="formLabelWidth" class="code">
          <el-input placeholder="请输入图片验证码" v-model="form.img_code" class="codeLeft"></el-input>
          <img :src="imgCode" class="codeRight" alt="" @click="getImgCode">
        </el-form-item>
        <el-form-item label="验证码" :label-width="formLabelWidth" class="code">
          <el-input placeholder="请输入手机验证码" v-model="form.code"></el-input>
          <div class="codeRight">
            <div class="codeBtn" @click="getCode" v-if="showCode">
              获取验证码
            </div>
            <div class="codeBtn outTime" v-else>
              {{outTime}}S后重新获取
            </div>
          </div>
        </el-form-item>
        <el-form-item label="" :label-width="'40px'">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入您要咨询的内容"
            v-model="textarea">
          </el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false">提 交</el-button>
      </div>
    </el-dialog>
    <!-- 咨询弹窗  -->

  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from '@/api/personal-api'
  import commonApi from '@/api/common-api'

  export default {
    name: "focus",
    data() {
      return {
        form: {},
        data:[],
        radio: '',
        dialogFormVisible: false,
        formLabelWidth: '80px',
        textarea:'',
        page: 0,
        count: 0,
        productid: '',
        showCode: true,
        outTime: 60,
        imgCode: ''
      }
    },
    mounted(){
      this.getData();
      this.getImgCode();
    },
    methods: {
      getData(){
        const {page} =this;
        api.getFocusList({page,type:2}).then(data =>{
          console.log("data",data);
          this.data = data.data;
          this.count = data.count;
        });
      },
      /**
       * 点击分页
       * @param e
       */
      pageChange:function(e){
        this.page = e-1;
        window.scrollTo(0,0);
        this.getData();
      },
      /**
       * 获取图片验证码
       */
      getImgCode: function(){
        commonApi.getImgCode().then(data => {
          this.imgCode = data;
        })
      },

      /**
       * 获取验证码
       */
      getCode: function() {
        console.log("this.form;",this.form);
        const {phone = '',img_code=''} = this.form;
        const _this = this;
        if (!this.validatePhone(phone)) {
          return
        }
        this.showCode = false;
        let intervalID = setInterval(function() {
          if (_this.outTime == 0) {
            _this.showCode = true;
            _this.outTime = 60;
            clearInterval(intervalID)
          }
          _this.outTime--;
        }, 1000)
        commonApi.getPhoneCode({phone,img_code}).then(data=>{
          this.$message.success('验证码获取成功');
        });
      },

      /**
       * 验证手机格式
       * @param {Object} phone
       */
      validatePhone: function(phone) {
        if (phone.length != 11) {
          this.$message.error('手机号码格式不正确');
          return false;
        }
        return true;
      },
      /**
       * 取消关注
       */
      cancelFocus: function({currentTarget: {dataset:{id}}}){
        console.log("id",id);
          api.cancelCollect({id,type:1}).then(data=>{
              this.$message.success("取消关注成功")
              this.getData();
          });
      }

    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style scoped type="text/less" lang="less">
  .focus {
    background-color: #F4F5FA;
    padding-bottom: 120px;

    .content {
      padding-top: 20px;

      .right {
        width: 950px;
        background-color: #fff;

        .header {
          height: 60px;
          line-height: 58px;
          color: #999;
          font-size: 16px;
          padding: 0 30px;

          .tab {
            margin-right: 45px;
          }

          .currentTab {
            color: #333;
            font-weight: bold;
            border-bottom: 2px solid #579EFA;
          }

        }
      }
    }
  }
  .per{

    padding: 0 30px;
    .item{
      position: relative;
      display: flex;
      font-size: 14px;
      color: #999;
      padding: 20px 0;
      border-bottom: 1px solid #E3E4E5;
      .right{
        margin-left: 22px;
        .name{
          font-size: 18px;
          color: #333;
          font-weight: bold;
          margin-top: 24px;
          .money{
            color: #FF7E00;
            font-size: 16px;

          }
        }
      }
      img{
        width: 140px;
        height: 140px;
      }
      .detail{
        width: 320px;
        margin-top: 15px;

        .info{
          display: inline-block;
          margin-bottom: 10px;
          span{
            display: inline-block;
            width: 80px;
          }
        }
      }
    }
    .btns{
      position: absolute;
      bottom: 30px;
      right: 0px;
      display: flex;
      text-align: right;
    }
    .item:last-child{
      border: 0;
    }
  }


  .dialog-footer {
    text-align: center;
  }
  .pages{
    text-align: center;
    margin: 34px 0;
  }
</style>



